<script>
import ImgUtil from "@/util/ImgUtil";
import FileUtil from "@/util/FileUtil";
import DownloadUtil from "@/util/DownloadUtil";
export default {
  data() {
    return {
      metaInfo: {
        title: ' - JPG/PNG/WebP',
        meta: [
          { name: 'description', content: '' },
          { name: 'keywords', content: ',JPG,PNG,WebP,,,' }
        ]
      },
      fileList: [],
      compressFileList : [],
      dialogImageUrl: '',
      dialogVisible: false,
      disabled: false,
      compressVal: 50,
      compressValue : 0,
      saveFormat: 'original', // 
      formatOptions: [ // 
        { label: '', value: 'original' },
        { label: 'image/jpeg', value: 'image/jpeg' },
        { label: 'image/jpg', value: 'image/jpg' },
        { label: 'image/png', value: 'image/png' },
        { label: 'image/webp', value: 'image/webp' },
        { label: 'image/gif', value: 'image/gif' },
      ]
    };
  },
  methods: {
    async startDownload() {
      this.compressFileList.forEach((item, idx) => {
        // 
        const originName = this.fileList[idx]?.name ? this.fileList[idx].name.replace(/\.[^.]+$/, '') : idx;
        const ext = this.saveFormat === 'original'
          ? FileUtil.getExt(this.fileList[idx]?.name || item.save_name)
          : (this.saveFormat.replace('image/', '') === 'jpg' ? 'jpg' : this.saveFormat.replace('image/', ''));
        const fileName = originName + '.' + ext;
        DownloadUtil.downloadBlob(item.file, fileName);
      });
    },
    async startDownloadAsZip() {
      const loading = this.$loading({
        lock: true,
        text: '...',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
      try {
        const files = this.compressFileList.map((item, idx) => {
          const originName = this.fileList[idx]?.name ? this.fileList[idx].name.replace(/\.[^.]+$/, '') : idx;
          const ext = this.saveFormat === 'original'
            ? FileUtil.getExt(this.fileList[idx]?.name || item.save_name)
            : (this.saveFormat.replace('image/', '') === 'jpg' ? 'jpg' : this.saveFormat.replace('image/', ''));
          const fileName = originName + '.' + ext;
          return { file: item.file, save_name: fileName };
        });
        const filename = DownloadUtil.generateTimestampFilename('compressed_images', 'zip');
        await DownloadUtil.downloadAsZip(files, filename, 'file', 'save_name');
      } catch (e) {
        console.log('startDownloadAsZip err' + e.message);
      }
      loading.close();
    },
    async startCompress() {
      this.compressFileList = [];
      let k = 0;
      const loading = this.$loading({
        lock: true,
        text: '...',
        spinner: 'el-icon-loading',
        background: 'rgba(0, 0, 0, 0.7)'
      });
      this.compressValue = this.compressVal;
      for (const file of this.fileList) {
        try {
          //  mimeType
          let mimeType = '';
          if (this.saveFormat === 'original') {
            mimeType = file.raw.type;
            if (!['image/png', 'image/jpeg', 'image/jpg', 'image/webp', 'image/gif'].includes(mimeType)) {
              mimeType = 'image/jpeg';
            }
          } else {
            mimeType = this.saveFormat;
          }
          // compressorjs  gif gif 
          let compressedFile;
          if (mimeType === 'image/gif') {
            compressedFile = file.raw;
          } else {
            compressedFile = await ImgUtil.compressImage(file.raw, (100 - this.compressValue)/100, mimeType);
          }
          // 
          const ext = mimeType === 'image/png' ? 'png'
                    : mimeType === 'image/jpeg' ? 'jpeg'
                    : mimeType === 'image/jpg' ? 'jpg'
                    : mimeType === 'image/webp' ? 'webp'
                    : mimeType === 'image/gif' ? 'gif'
                    : FileUtil.getExt(file.name);
          const imgBlob = ImgUtil.fileToBlob(compressedFile);
          this.compressFileList.push({
            before_size: ImgUtil.formatBytes(file.size),
            file: imgBlob,
            save_name : k + '.' + ext,
            file_url : URL.createObjectURL(imgBlob),
            after_size: ImgUtil.formatBytes(compressedFile.size || compressedFile.size),
            k : k++,
          });
        } catch (error) {
          console.error('Compression Error:', error.message);
        }
      }
      loading.close();
    },
    handleRemove(file) {
      console.log(file);
    },
    handlePictureCardPreview(file) {
      this.dialogImageUrl = file.url;
      this.dialogVisible = true;
    },
    handleDownload(file) {
      console.log(file);
    },
    handleChange(file, files) {
      this.fileList = files;
    },
  }
}
</script>

<template>
  <div class="modern-bg">
    <el-row :gutter="0" justify="center">
      <el-col :xs="{span: 22, offset: 1}" :sm="{span: 20, offset: 2}" :md="{span: 16, offset: 4}" :lg="{span: 12, offset: 6}" :xl="{span: 10, offset: 7}">
        <el-card shadow="hover" class="modern-card">
          <h2 class="modern-title"></h2>
          <el-divider></el-divider>
          <el-upload
              :auto-upload="false"
              :multiple="true"
              action="#"
              list-type="picture-card"
              :on-preview="handlePictureCardPreview"
              :on-remove="handleRemove"
              :on-change="handleChange"
              class="modern-upload"
          >
            <i slot="default" class="el-icon-plus"></i>
          </el-upload>
          <el-dialog :visible.sync="dialogVisible">
            <img width="100%" :src="dialogImageUrl" alt="">
          </el-dialog>
          <div class="block modern-slider-block">
            <div>
              <span class="demonstration"></span>
              <el-slider
                  v-model="compressVal"
                  show-input
                  class="modern-slider"
              ></el-slider>
            </div>
            <div>
               <span class="demonstration"></span>
              <el-select v-model="saveFormat" placeholder="" style="width: 180px;">
                <el-option v-for="item in formatOptions" :key="item.value" :label="item.label" :value="item.value" />
              </el-select>
            </div>
            
           
            <div class="modern-btn-group">
              <el-button type="primary" round @click="startCompress"></el-button>
              <el-button type="success" round @click="startDownload" v-show="compressFileList.length > 0"></el-button>
              <el-button type="warning" round @click="startDownloadAsZip" v-show="compressFileList.length > 0"></el-button>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
    <el-row :gutter="0" justify="center">
      <el-col :xs="{span: 22, offset: 1}" :sm="{span: 20, offset: 2}" :md="{span: 16, offset: 4}" :lg="{span: 12, offset: 6}" :xl="{span: 10, offset: 7}">
        <el-card shadow="hover" class="modern-card">
          <h3 class="modern-title">{{compressValue}}%</h3>
          <el-divider></el-divider>
          <el-row :gutter="16" class="row-bg">
            <el-col :xs="{span:12}" :sm="{span:8}" :md="{span:6}" :lg="{span:6}" :xl="{span:4}" v-for="(item) in compressFileList" :key="item.k">
              <el-card shadow="always" class="img-card">
                <el-image
                    style="width: 100px; height: 100px; border-radius: 12px; box-shadow: 0 2px 8px rgba(0,0,0,0.08);"
                    :src="item.file_url"
                    :preview-src-list="compressFileList.map(it=>it.file_url)"
                    fit="scale-down"></el-image>
                <div class="img-info">:{{item.before_size}}</div>
                <div class="img-info">:{{item.after_size}}</div>
              </el-card>
            </el-col>
          </el-row>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<style scoped>
.modern-bg {
  //min-height: 100vh;
  background: #f6f8fa;
  //padding: 32px 0;
}
.modern-card {
  border-radius: 16px;
  box-shadow: 0 4px 24px rgba(0,0,0,0.08);
  margin-bottom: 32px;
}
.modern-title {
  text-align: center;
  font-weight: 600;
  color: #222;
  margin-bottom: 0;
}
.modern-slider-block {
  margin-top: 24px;
  margin-bottom: 8px;
}
.modern-slider {
  width: 80%;
  margin: 0 auto 16px auto;
}
.modern-btn-group {
  display: flex;
  justify-content: center;
  margin-top: 16px;
}
.img-card {
  border-radius: 12px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.08);
  text-align: center;
  margin-bottom: 16px;
}
.img-info {
  font-size: 13px;
  color: #888;
  margin: 4px 0 0 0;
}
</style>
