<script>
import Upscaler from 'upscaler';
import FileUtil from "@/util/FileUtil";

export default {
  name: 'UpscaleView',
  data() {
    return {
      metaInfo: {
        title: ' - AI',
        meta: [
          { name: 'description', content: ' AI ' },
          { name: 'keywords', content: ',AI,,,' }
        ]
      },
      imgs: [],
      multipleSelection: [],
      upscaler: null,
      loadingModel: false,
      modelLoaded: false,
      modelProgress: {
        key: 'UpscalerJS',
        current: '',
        total: '',
        progress: ''
      },
      webglNotSupported: false, // WebGL
      aiFailed: false, // AI
      tileWidth: 100, // 100px
      tileHeight: 100, // 100px
      maxRetries: 3 // 3
    }
  },
  async mounted() {
    if (window.__PRERENDER_INJECTED__)
    {
      return;
    }
    // localStorage

    // WebGL
    this.webglNotSupported = !(window.WebGLRenderingContext && document.createElement('canvas').getContext('webgl'));
    if (this.webglNotSupported) {

      return;
    }

    // WebGL
    this.setupWebGLContextListeners();

    this.loadingModel = true;
    // 
    this.modelProgress.current = '';
    this.modelProgress.total = '';
    this.modelProgress.progress = '0';

    try {
      this.upscaler = new Upscaler();
      if ( this.webglNotSupported === false) {
        await this.upscaler.upscale(document.createElement('canvas'));
      }
      this.modelLoaded = true;
      this.modelProgress.current = '';
      this.modelProgress.progress = '100';
    } catch (error) {
      console.error(':', error);
      this.webglNotSupported = true;

    } finally {
      this.loadingModel = false;
    }
  },
  methods: {
    formatFileSize(bytes) {
      if (bytes === 0) return '0 B';
      const thresholds = {
        'TB': Math.pow(1024, 4),
        'GB': Math.pow(1024, 3),
        'MB': Math.pow(1024, 2),
        'KB': 1024,
        'B' : 1,
      };
      function getSizeName(size) {
        for (let unit in thresholds) {
          if (size >= thresholds[unit]) {
            return unit;
          }
        }
      }
      const unit = getSizeName(bytes);
      const size = Math.round((bytes / thresholds[unit]) * 100) / 100;
      return `${size} ${unit}`;
    },
    handleUploadChange(file, fileList) {
      this.imgs = [];
      for (const f of fileList) {
        const rawFile = f.raw || f;
        let imgInfo = {
          img: URL.createObjectURL(rawFile),
          imgFile: rawFile,
          upscaleImg: null,
          upscaleImgFile: null,
          imgError: false,
          startTime: null,
          endTime: null,
          processing: false,
          msg: '',
          progress: 0,
          currentTile: 0,
          totalTiles: 0,
          currentTileX: 0,
          currentTileY: 0,
        };
        if (rawFile.type && rawFile.type.startsWith('image/gif')) {
          imgInfo.imgError = true;
          imgInfo.msg = 'gif';
        }
        this.imgs.push(imgInfo);
      }
    },
    isProcessing() {
      return this.imgs.some(img => img.processing);
    },
    //  - FUCK
    async upscaleMany() {
      console.log(` ${this.imgs.length}`);

      // 
      for (let i = 0; i < this.imgs.length; i++) {
        const img = this.imgs[i];

        // 
        if (img.upscaleImg || img.processing) {
          console.log(` ${i + 1}/${this.imgs.length}: ${img.imgFile.name} ()`);
          continue;
        }

        console.log(` ${i + 1}/${this.imgs.length}: ${img.imgFile.name}`);

        try {
          await this.upscale(img);
          console.log(` ${i + 1}/${this.imgs.length}: ${img.imgFile.name}`);
        } catch (error) {
          console.error(` ${i + 1}/${this.imgs.length}: ${img.imgFile.name}`, error);
          img.msg = ': ' + error.message;
          img.processing = false;
        }

        // 
        if (i < this.imgs.length - 1) {
          console.log(` 1...`);
          await new Promise(resolve => setTimeout(resolve, 1000));
        }
      }

      console.log(` ${this.imgs.length}`);
    },
    //  - 
    async upscale(item) {
      try {
        if (item.upscaleImg !== null) return;
        if (item.imgError) {
          item.msg = '';
          return;
        }
        if (!item.img) {
          item.msg = '';
          return;
        }
        if (item.processing) return;

        item.msg = '...';
        item.processing = true;
        item.progress = 0;
        item.startTime = (new Date()).getTime();

        console.log(` : ${item.imgFile.name}`);

        //  HTMLImageElement
        const imgEl = new window.Image();
        imgEl.src = item.img;

        // 
        await new Promise((resolve, reject) => {
          imgEl.onload = resolve;
          imgEl.onerror = reject;
        });

        console.log(` : ${imgEl.width}x${imgEl.height}`);

        // 
        const { tileWidth, tileHeight, needTiling, warning } = this.calculateOptimalTileSize(imgEl.width, imgEl.height);

        console.log(` :`);
        console.log(`   : ${imgEl.width}x${imgEl.height}`);
        console.log(`   : ${needTiling ? '' : ''}`);
        console.log(`   : ${tileWidth}x${tileHeight}`);

        // 
        if (warning) {
          console.log(` : ${warning}`);
          this.$message.warning(warning);
        }

        if (needTiling) {
          //  - 
          console.log(` `);
          await this.processLargeImage(imgEl, item, tileWidth, tileHeight);
          console.log(` : ${item.imgFile.name}`);
        } else {
          // 
          console.log(` `);
          await this.processSmallImage(imgEl, item);
          console.log(` : ${item.imgFile.name}`);
        }
      } catch (error) {
        console.error(` : ${item.imgFile.name}`, error);
        // blob
        if (item.upscaleImg) URL.revokeObjectURL(item.upscaleImg);
        item.upscaleImg = null;
        item.upscaleImgFile = null;
        item.processing = false;

        // 
        if (error.message.includes('')) {
          item.msg = '';
          this.$message.error('1024x1024');
        } else if (error.message.includes('WebGL') || error.message.includes('shader') || error.message.includes('context')) {
          this.webglNotSupported = true;
          this.aiFailed = true;
          item.msg = 'WebGLGPU';
          this.$message.error('WebGLGPU');
        } else {
          item.msg = '';
          this.aiFailed = true;
          this.$message.error('');
        }

        // 
        if (window.gc) window.gc();

        // 
        throw error;
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleDeleteRow(index) {
      const img = this.imgs[index];

      //  el-upload 
      //  fileList  rawFile 
      if (this.$refs.uploadRef) {
        //  el-upload  fileList 
        //  fileList  raw 
        const fileToRemove = this.$refs.uploadRef.uploadFiles.find(f => f.raw === img.imgFile);
        if (fileToRemove) {
          this.$refs.uploadRef.handleRemove(fileToRemove);
        }
      }

      if (img.upscaleImg) URL.revokeObjectURL(img.upscaleImg);

      // 
      this.imgs.splice(index, 1);

      if (window.gc) window.gc();
    },
    handleBatchDelete() {
      // 1.  el-upload 
      this.multipleSelection.forEach(img => {
        //  URL  URL.createObjectURL() 
        if (img.img) URL.revokeObjectURL(img.img);
        if (img.upscaleImg) URL.revokeObjectURL(img.upscaleImg);

        //  el-upload 
        if (this.$refs.uploadRef) {
          //  el-upload  fileList 
          //  img.imgFile  File/Blob 
          const fileToRemove = this.$refs.uploadRef.uploadFiles.find(f => f.raw === img.imgFile);

          if (fileToRemove) {
            //  handleRemove  el-upload 
            this.$refs.uploadRef.handleRemove(fileToRemove);
          }
        }
      });

      // 2.  imgs
      // 
      this.imgs = this.imgs.filter(img => !this.multipleSelection.includes(img));

      // 3. 
      this.multipleSelection = [];
      this.$refs.imgTable && this.$refs.imgTable.clearSelection();

      // 4. 
      if (window.gc) window.gc();
    },

    // 
    calculateOptimalTileSize(width, height) {
      const maxPixels = width * height;
      const megaPixels = (maxPixels / (1024 * 1024)).toFixed(1);
      let warning = null;

      // 
      const tileWidth = this.tileWidth;
      const tileHeight = this.tileHeight;

      // 
      const tilesX = Math.ceil(width / tileWidth);
      const tilesY = Math.ceil(height / tileHeight);
      const totalTiles = tilesX * tilesY;

      // 
      if (totalTiles > 10000) {
        warning = `${megaPixels}MP${totalTiles}`;
      } else if (totalTiles > 5000) {
        warning = `${megaPixels}MP${totalTiles}`;
      } else if (totalTiles > 1000) {
        warning = `${megaPixels}MP${totalTiles}`;
      } else if (totalTiles > 100) {
        warning = `${megaPixels}MP${totalTiles}`;
      }

      console.log(` :`);
      console.log(`   : ${width}x${height}`);
      console.log(`   : ${tileWidth}x${tileHeight} ()`);
      console.log(`   : ${tilesX}x${tilesY} = ${totalTiles}`);
      console.log(`   `);

      // 
      return { tileWidth, tileHeight, needTiling: true, warning };
    },

    // 
    async processSmallImage(imgEl, item) {
      console.log(` :`);
      console.log(`   : ${imgEl.width}x${imgEl.height}`);
      console.log(`   : `);

      // 
      item.progress = 10;
      item.msg = '...';

      console.log(` ...`);
      // upscalerbase64
      item.progress = 50;
      const upscaledBase64 = await this.upscaler.upscale(imgEl);

      console.log(` `);
      item.progress = 80;
      // upscaledBase64  base64 
      item.upscaleImg = upscaledBase64;

      // base64blob
      const response = await fetch(upscaledBase64);
      const blob = await response.blob();
      item.upscaleImgFile = blob;

      item.endTime = (new Date()).getTime();
      item.processing = false;
      item.progress = 100;
      item.msg = '';
      console.log(` : ${Math.floor((item.endTime - item.startTime) / 1000)}`);
    },

    // 
    async processLargeImage(imgEl, item, tileWidth, tileHeight) {
      const { width, height } = imgEl;
      const scale = 2; // 

      // 
      const tilesX = Math.ceil(width / tileWidth);
      const tilesY = Math.ceil(height / tileHeight);
      const totalTiles = tilesX * tilesY;

      // 
      const estimatedMinutes = Math.ceil(totalTiles * 3 / 60); // 3

      item.msg = ` ${tileWidth}x${tileHeight} (${tilesX}x${tilesY}=${totalTiles}, ${estimatedMinutes})`;
      item.progress = 0;
      item.startTime = (new Date()).getTime();
      item.totalTiles = totalTiles;
      item.currentTile = 0;

      // 
      console.log(` ${tileWidth}x${tileHeight}:`);
      console.log(`   : ${width}x${height}`);
      console.log(`   : ${tileWidth}x${tileHeight} ()`);
      console.log(`   : ${tilesX}x${tilesY} = ${totalTiles}`);
      console.log(`   : ${estimatedMinutes}`);
      console.log(`   : ${this.maxRetries}`);
      console.log(`   : /`);

      // canvas
      const finalCanvas = document.createElement('canvas');
      finalCanvas.width = width * scale;
      finalCanvas.height = height * scale;
      const finalCtx = finalCanvas.getContext('2d');

      let processedTiles = 0;

      // 
      for (let y = 0; y < tilesY; y++) {
        for (let x = 0; x < tilesX; x++) {
          // 
          const startX = x * tileWidth;
          const startY = y * tileHeight;
          const currentTileWidth = Math.min(tileWidth, width - startX);
          const currentTileHeight = Math.min(tileHeight, height - startY);

          // 
          console.log(` ${processedTiles + 1} [${x + 1},${y + 1}]:`);
          console.log(`   : (${startX}, ${startY})`);
          console.log(`   : ${currentTileWidth}x${currentTileHeight}`);
          console.log(`   : X[${startX}-${startX + currentTileWidth}], Y[${startY}-${startY + currentTileHeight}]`);

          // canvas
          const tileCanvas = document.createElement('canvas');
          tileCanvas.width = currentTileWidth;
          tileCanvas.height = currentTileHeight;
          const tileCtx = tileCanvas.getContext('2d');

          // 
          tileCtx.drawImage(
            imgEl,
            startX, startY, currentTileWidth, currentTileHeight,
            0, 0, currentTileWidth, currentTileHeight
          );

          // 
          let retryCount = 0;
          let upscaledTileBase64 = null;
          let tileProcessed = false;

          while (retryCount < this.maxRetries && !tileProcessed) {
            try {
              // 
              const retryText = retryCount > 0 ? ` (${retryCount}/${this.maxRetries})` : '';
              console.log(` ${processedTiles + 1}[${x + 1},${y + 1}]${retryText}...`);

              upscaledTileBase64 = await this.upscaler.upscale(tileCanvas);
              tileProcessed = true;

              if (retryCount > 0) {
                console.log(` ${processedTiles + 1}${retryCount}`);
              }
            } catch (error) {
              retryCount++;
              console.error(` ${processedTiles + 1} (${retryCount}/${this.maxRetries}):`, error.message);

              if (retryCount < this.maxRetries) {
                console.log(` ${processedTiles + 1}...`);
                // 
                await new Promise(resolve => setTimeout(resolve, 1000));
              } else {
                console.error(` ${processedTiles + 1}${this.maxRetries}`);
                break;
              }
            }
          }

          if (tileProcessed && upscaledTileBase64) {
            try {
              // 
              const upscaledImg = new Image();
              await new Promise((resolve, reject) => {
                upscaledImg.onload = resolve;
                upscaledImg.onerror = reject;
                upscaledImg.src = upscaledTileBase64;
              });

              // 
              console.log(` ${processedTiles + 1}:`);
              console.log(`   : ${currentTileWidth}x${currentTileHeight}`);
              console.log(`   : ${upscaledImg.width}x${upscaledImg.height}`);
              console.log(`   : ${(upscaledImg.width / currentTileWidth).toFixed(1)}x`);

              // 
              console.log(` ${processedTiles + 1}...`);
              finalCtx.drawImage(
                upscaledImg,
                startX * scale, startY * scale,
                currentTileWidth * scale, currentTileHeight * scale
              );

              // 
              const currentResultBase64 = finalCanvas.toDataURL('image/jpeg', 0.9);
              item.upscaleImg = currentResultBase64;
              console.log(` : ${processedTiles + 1}/${totalTiles}`);

              // 
              console.log(` ${processedTiles + 1}`);
              tileCanvas.width = 0;
              tileCanvas.height = 0;
              upscaledImg.src = '';

            } catch (error) {
              console.error(`${processedTiles + 1}:`, error);
              // 
              finalCtx.drawImage(
                imgEl,
                startX, startY, currentTileWidth, currentTileHeight,
                startX * scale, startY * scale, currentTileWidth * scale, currentTileHeight * scale
              );
            }
          } else {
            // 
            console.log(` ${processedTiles + 1}`);
            finalCtx.drawImage(
              imgEl,
              startX, startY, currentTileWidth, currentTileHeight,
              startX * scale, startY * scale, currentTileWidth * scale, currentTileHeight * scale
            );
          }

          processedTiles++;

          //  = /
          item.progress = Math.round((processedTiles / totalTiles) * 100);

          // 
          const currentTime = (new Date()).getTime();
          const elapsedSeconds = (currentTime - item.startTime) / 1000;
          const avgTimePerTile = elapsedSeconds / processedTiles;
          const remainingTiles = totalTiles - processedTiles;
          const remainingMinutes = Math.ceil((remainingTiles * avgTimePerTile) / 60);

          // 
          const currentTileX = x + 1;
          const currentTileY = y + 1;
          item.msg = ` [${currentTileX},${currentTileY}] ${processedTiles}/${totalTiles} (${item.progress}%) ${remainingMinutes}`;
          item.currentTile = processedTiles;
          item.totalTiles = totalTiles;
          item.currentTileX = currentTileX;
          item.currentTileY = currentTileY;

          // 
          console.log(` : ${processedTiles}/${totalTiles} - [${currentTileX},${currentTileY}] - ${currentTileWidth}x${currentTileHeight} - ${item.progress}% (${processedTiles}/${totalTiles}) - ${remainingMinutes}`);

          // UI
          await new Promise(resolve => setTimeout(resolve, 10));

          // 
          if (processedTiles % 4 === 0 && window.gc) {
            window.gc();
          }
        }
      }

      // blob
      finalCanvas.toBlob(blob => {
        item.upscaleImg = URL.createObjectURL(blob);
        item.upscaleImgFile = blob;
        item.endTime = (new Date()).getTime();
        item.processing = false;
        item.progress = 100;
        item.msg = '';

        // canvas
        finalCanvas.width = 0;
        finalCanvas.height = 0;
      }, 'image/png');
    },

    // WebGL
    setupWebGLContextListeners() {
      // WebGL
      window.addEventListener('webglcontextlost', (event) => {
        event.preventDefault();
        console.warn('WebGLAI');
        this.webglNotSupported = true;
        this.aiFailed = true;
        this.$message.error('GPUWebGL');
      });

      // WebGL
      window.addEventListener('webglcontextrestored', () => {
        console.log('WebGL');
        this.$message.info('WebGL');
      });
    },

    // 
    async downloadImg(item) {
      if (!item.upscaleImgFile || !item.imgFile) {
        this.$message.warning('');
        return;
      }
      const fileName = item.imgFile.name.replace(/\.[^.]+$/, '') + '_hd.png';
      await FileUtil.downloadFile(item.upscaleImgFile, fileName);
    },

    // 
    async handleDownloadZip() {
      const files = this.imgs.filter(img => img.upscaleImgFile && img.imgFile && img.imgFile.name).map(img => ({
        file: img.upscaleImgFile,
        save_name: img.imgFile.name.replace(/\.[^.]+$/, '') + '_hd.png'
      }));
      if (files.length === 0) {
        this.$message.warning('');
        return;
      }
      await FileUtil.downloadAsZip(files, `upscale_${Date.now()}.zip`, 'file', 'save_name');
    }
  },
  beforeDestroy() {
    this.imgs.forEach(img => {
      if (img.upscaleImg) URL.revokeObjectURL(img.upscaleImg);
    });

    // upscaler
    if (this.upscaler && this.upscaler.terminate) {
      this.upscaler.terminate();
    }

    // 
    if (window.gc) window.gc();
  }
}
</script>

<template>
  <div class="modern-bg">
    <el-row :gutter="0" justify="center">
      <el-col :xs="{span: 24, offset: 0}" :sm="{span: 22, offset: 1}" :md="{span: 18, offset: 3}" :lg="{span: 14, offset: 5}" :xl="{span: 12, offset: 6}">
        <el-card shadow="hover" class="modern-card">
          <div style="color: #f56c6c; font-size: 16px; margin-bottom: 12px; text-align: center;">
            ;;
          </div>
          <div v-if="webglNotSupported" style="color: #f56c6c; font-size: 16px; margin-bottom: 12px; text-align: center;">
             WebGL
          </div>
          <!--  -->
          <div class="tile-settings" v-if="!webglNotSupported">
            <div class="setting-row">
              <span class="setting-label"> </span>
              <el-input-number
                v-model="tileWidth"
                :min="100"
                :max="500"
                :step="10"
                size="small"
                :disabled="isProcessing() || loadingModel || aiFailed"
                style="width: 120px; margin-right: 8px;"
              />
              <span style="margin: 0 4px;"></span>
              <el-input-number
                v-model="tileHeight"
                :min="100"
                :max="500"
                :step="10"
                size="small"
                :disabled="isProcessing() || loadingModel || aiFailed"
                style="width: 120px; margin-right: 8px;"
              />
              <span class="setting-hint"> (100100,)</span>
            </div>
            <div class="setting-row">
              <span class="setting-label"> </span>
              <el-input-number
                v-model="maxRetries"
                :min="1"
                :max="5"
                size="small"
                :disabled="isProcessing() || loadingModel || aiFailed"
                style="width: 120px; margin-right: 8px;"
              />
              <span class="setting-hint"> ()</span>
            </div>
          </div>

          <div class="upload-row">
            <template v-if="!webglNotSupported">
              <el-upload
                ref="uploadRef"
                action="#"
                :auto-upload="false"
                :multiple="true"
                :show-file-list="false"
                accept="image/*"
                :disabled="isProcessing() || loadingModel || aiFailed"
                :on-change="handleUploadChange"
              >
                <el-button type="primary" round :disabled="isProcessing() || loadingModel || aiFailed"></el-button>
              </el-upload>
              <el-button type="success" round :disabled="isProcessing() || imgs.length === 0 || loadingModel || aiFailed" @click="upscaleMany"></el-button>
              <el-button size="mini" :disabled="aiFailed" @click="$refs.imgTable.clearSelection()"></el-button>
              <el-button type="danger" size="mini" :disabled="multipleSelection.length === 0 || aiFailed" @click="handleBatchDelete"></el-button>
              <el-button type="warning" size="mini" :disabled="imgs.filter(img => img.upscaleImgFile).length === 0 || aiFailed" @click="handleDownloadZip"></el-button>

            </template>
          </div>
          <div style="margin-top: 24px;">
            <el-table ref="imgTable" :data="imgs" border style="width: 100%; max-width: 900px; margin: 0 auto;">
              <el-table-column type="selection" width="50"></el-table-column>
              <el-table-column label="" align="center">
                <template slot-scope="scope">
                  <el-image
                    style="width: 120px; border-radius: 10px; box-shadow: 0 2px 8px rgba(0,0,0,0.08);"
                    :src="scope.row.img"
                    v-if="scope.row.img"
                    :alt="scope.row.imgFile.name"
                    :preview-src-list="imgs.map(i => i.img)"
                    @error="scope.row.imgError=true;scope.row.msg=';'"
                  />
                  <p class="img-info" v-if="scope.row.imgFile">:{{formatFileSize(scope.row.imgFile.size)}}</p>
                </template>
              </el-table-column>
              <el-table-column label="" align="center">
                <template slot-scope="scope">
                  <div v-if="scope.row.upscaleImg">
                    <el-image
                      style="width: 120px; border-radius: 10px; box-shadow: 0 2px 8px rgba(0,0,0,0.08); background: #f0f9eb;"
                      :alt="''+scope.row.imgFile.name"
                      :src="scope.row.upscaleImg"
                      :preview-src-list="imgs.map(i => i.upscaleImg).filter(Boolean)"
                    />
                    <!--  -->
                    <div v-if="scope.row.processing" style="font-size: 11px; color: #E6A23C; margin: 3px 0;">
                       ...
                    </div>
                    <!--  -->
                    <el-button v-if="!scope.row.processing && scope.row.upscaleImgFile" type="primary" size="mini" @click="downloadImg(scope.row)" style="margin-top: 5px;">
                      <i class="el-icon-download"></i> 
                    </el-button>
                    <p class="img-info" v-if="scope.row.upscaleImgFile">:{{formatFileSize(scope.row.upscaleImgFile.size)}}</p>
                  </div>
                  <div v-else-if="scope.row.processing" style="color: #E6A23C; font-size: 12px;">
                     ...
                  </div>
                  <div v-else style="color: #999; font-size: 12px;">
                    ...
                  </div>
                </template>
              </el-table-column>
              <el-table-column label="" align="center" width="300">
                <template slot-scope="scope">
                  <div style="text-align: center;">
                    <h3 style="color: #409EFF; margin: 5px 0; font-size: 14px;">{{scope.row.msg}}</h3>

                    <!--  -->
                    <div v-if="scope.row.processing && scope.row.totalTiles > 1" style="font-size: 12px; color: #E6A23C; margin: 5px 0;">
                       : {{scope.row.totalTiles}}{{scope.row.currentTile}}
                      <br>
                       : [{{scope.row.currentTileX}},{{scope.row.currentTileY}}]
                    </div>

                    <!--  -->
                    <div v-if="scope.row.processing && scope.row.progress >= 0">
                      <el-progress
                        :percentage="scope.row.progress"
                        :stroke-width="12"
