<script>
import imgRemoveBackground, { preload } from '@imgly/background-removal';
import FileUtil from "@/util/FileUtil";


export default {
  name: 'App',
  data() {
    return {
      metaInfo: {
        title: ' -  ',
        meta: [
          { name: 'description', content: '' },
          { name: 'keywords', content: ',,,AI,,' }
        ]
      },
      progress: {},
      imgs : [],
      preloaded : false,
      multipleSelection: [] // 
    }
  },
  mounted() {
    if (window.__PRERENDER_INJECTED__)
    {
      return;
    }
    let config = {
      debug: true,// enable or disable useful console.log outputs
      proxyToWorker: true, // Whether to proxy the calculations to a web worker. (Default true)
      model: 'medium', // The model to use. (Default "medium")
      output: {
        format: 'image/png', // The output format. (Default "image/png")
        quality: 1, // The quality. (Default: 0.8)
        type: 'foreground', // The output type. (Default "foreground")
      },
      progress: (key, current, total) => {
        if (this.progress[key] === undefined) {
          this.progress[key] = {
            key: key,
            current: 0,
            total: 0,
          };
        }
        this.progress[key] = {
          key: key,
          current: this.formatFileSize(current),
          total: this.formatFileSize(total),
          progress : (current * 100/total)+'%',
        };
        console.log(`Downloading ${key}: ${current} of ${total}`);
      }
    };
    //
    preload(config).then(() => {
      this.preloaded = true;
      console.log("Background removal resources preloaded");
    });
  },
  methods: {
    formatFileSize(bytes) {
      if (bytes === 0) return '0 B';
      const thresholds = {
        'TB': Math.pow(1024, 4),
        'GB': Math.pow(1024, 3),
        'MB': Math.pow(1024, 2),
        'KB': 1024,
        'B' : 1,
      };

      function getSizeName(size) {
        for (let unit in thresholds) {
          if (size >= thresholds[unit]) {
            return unit;
          }
        }
      }

      const unit = getSizeName(bytes);
      const size = Math.round((bytes / thresholds[unit]) * 100) / 100; // 
      return `${size} ${unit}`;
    },
    //  el-upload
    handleUploadChange(file, fileList) {
      this.imgs = [];
      for (const f of fileList) {
        const rawFile = f.raw || f;
        let imgInfo = {
          img: URL.createObjectURL(rawFile),
          imgFile: rawFile,
          snapImg: null,
          snapImgSize: 0,
          snapImgFile: null,
          imgError: false,
          startTime: (new Date()).getTime(),
          endTime: (new Date()).getTime(),
          timer: null,
          snapping: false,
          msg: '',
        };
        if (rawFile.type && rawFile.type.startsWith('image/gif')) {
          imgInfo.imgError = true;
          imgInfo.msg = 'gif';
        }
        this.imgs.push(imgInfo);
      }
    },
    isSnaping() {
      for (const img of this.imgs) {
        if (img.snapping) {
          return true;
        }
      }
      return false;
    },

    snapMany() {
      this.imgs.forEach((img)=>{
        this.snap(img);
      });
    },

    async snap(item) {
      try {
        if (item.snapImg !== null) {
          //
          return;
        }
        if (item.imgError) {
          item.msg = '';
          return;
        }
        if (!item.img) {
          item.msg = ''
          return;
        }
        if (item.snapping) {
          return;
        }
        console.log(30);
        item.msg = '...'
        item.snapping = true;
        item.startTime = (new Date()).getTime();
        item.timer = setInterval(() => {
          item.endTime = (new Date()).getTime();
        });
        const blob = await imgRemoveBackground(item.img)
        clearInterval(item.timer);
        item.timer = null;
        item.endTime = (new Date()).getTime();
        console.log(32);
        // The result is a blob encoded as PNG. It can be converted to an URL to be used as HTMLImage.src
        item.snapImg = URL.createObjectURL(blob);
        item.snapImgFile = blob;
        item.snapping = false;
        item.msg = '';
      } catch (e) {
        item.msg = e.msg
        if (item.timer) {
          clearInterval(item.timer);
          item.timer = null;
        }
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleDeleteRow(index) {
      this.imgs.splice(index, 1);
    },
    handleBatchDelete() {
      this.multipleSelection.forEach(img => {
        if (img.img) URL.revokeObjectURL(img.img);
        if (img.snapImg) URL.revokeObjectURL(img.snapImg);
      });
      this.imgs = this.imgs.filter(img => !this.multipleSelection.includes(img));
      this.multipleSelection = [];
    },
    async handleDownloadZip() {
      // 
      const files = this.imgs.filter(img => img.snapImgFile && img.imgFile && img.imgFile.name).map(img => ({
        file: img.snapImgFile,
        save_name: img.imgFile.name
      }));
      if (files.length === 0) {
        this.$message.warning('');
        return;
      }
      await FileUtil.downloadAsZip(files, `matting_${Date.now()}.zip`, 'file', 'save_name');
    }
  }
}
</script>

<template>
  <div class="modern-bg">
    <el-row :gutter="0" justify="center">
      <el-col :xs="{span: 24, offset: 0}" :sm="{span: 22, offset: 1}" :md="{span: 18, offset: 3}" :lg="{span: 14, offset: 5}" :xl="{span: 12, offset: 6}">
        <el-card shadow="hover" class="modern-card">
          <div class="upload-row">
            <el-upload
              action="#"
              :auto-upload="false"
              :multiple="true"
              :show-file-list="false"
              accept="image/*"
              :disabled="isSnaping()"
              :on-change="handleUploadChange"
            >
              <el-button type="primary" round :disabled="isSnaping()"></el-button>
            </el-upload>
            <el-button type="success" round :disabled="isSnaping() || imgs.length === 0" @click="snapMany"></el-button>
            <el-button size="mini" @click="$refs.imgTable.clearSelection()"></el-button>
            <el-button type="danger" size="mini" :disabled="multipleSelection.length === 0" @click="handleBatchDelete"></el-button>
            <el-button type="warning" size="mini" :disabled="imgs.filter(img => img.snapImgFile).length === 0" @click="handleDownloadZip"></el-button>
          </div>
          <div style="margin-top: 24px;">
            <el-table ref="imgTable" :data="imgs" border style="width: 100%; max-width: 900px; margin: 0 auto;">
              <el-table-column type="selection" width="30"></el-table-column>
              <el-table-column label="" align="center">
                <template slot-scope="scope">
                  <el-image
                    style="width: 120px;max-width: 100%; border-radius: 10px; box-shadow: 0 2px 8px rgba(0,0,0,0.08);"
                    :src="scope.row.img"
                    v-if="scope.row.img"
                    :alt="scope.row.imgFile.name"
                    :preview-src-list="imgs.map(i => i.img)"
                    @error="scope.row.imgError=true;scope.row.msg=';'"
                  />
                  <p class="img-info" v-if="scope.row.imgFile">:{{formatFileSize(scope.row.imgFile.size)}}</p>
                </template>
              </el-table-column>
              <el-table-column label="" align="center">
                <template slot-scope="scope">
                  <el-image
                    style="width: 120px; max-width: 100%; border-radius: 10px; box-shadow: 0 2px 8px rgba(0,0,0,0.08); background: #f0f9eb;"
                    :alt="''+scope.row.imgFile.name"
                    :src="scope.row.snapImg"
                    v-if="scope.row.snapImg"
                    :preview-src-list="imgs.map(i => i.snapImg).filter(Boolean)"
                  />
                  <p class="img-info" v-if="scope.row.snapImgFile">:{{formatFileSize(scope.row.snapImgFile.size)}}</p>
                </template>
              </el-table-column>
              <el-table-column label="" align="center">
                <template slot-scope="scope">
                  <h2 style="color: red;">{{scope.row.msg}}</h2>
                  <span v-if="scope.row.snapImg || scope.row.snapping">:{{(scope.row.endTime - scope.row.startTime)/1000}}!</span>
                </template>
              </el-table-column>
              <el-table-column label="" align="center" width="90">
                <template slot-scope="scope">
                  <el-button type="danger" size="mini" @click="handleDeleteRow(scope.$index)"></el-button>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-card>
      </el-col>
    </el-row>
    <el-row :gutter="0" justify="center">
      <el-col :xs="{span: 24, offset: 0}" :sm="{span: 22, offset: 1}" :md="{span: 18, offset: 3}" :lg="{span: 14, offset: 5}" :xl="{span: 12, offset: 6}">
        <el-card shadow="hover" class="modern-card">
          <h3 class="modern-title">({{preloaded ? '':''}})</h3>
          <el-divider></el-divider>
          <el-table :data="Object.values(progress)" border style="width: 100%; max-width: 900px; margin: 0 auto;">
            <el-table-column label="" prop="key" align="center" />
            <el-table-column label="" align="center">
              <template slot-scope="scope">{{ scope.row.current }}/{{ scope.row.total }}</template>
            </el-table-column>
            <el-table-column label="" prop="progress" align="center" />
          </el-table>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<style scoped>
.modern-bg {
  //min-height: 100vh;
  background: #f6f8fa;
  //padding: 32px 0;
}
.modern-card {
  border-radius: 16px;
  box-shadow: 0 4px 24px rgba(0,0,0,0.08);
  margin-bottom: 32px;
}
.modern-title {
  text-align: center;
  font-weight: 600;
  color: #222;
  margin-bottom: 0;
}
.upload-row {
  display: flex;
  align-items: center;
  gap: 16px;
  flex-wrap: wrap;
  justify-content: center;
}
.img-info {
  font-size: 13px;
  color: #888;
  margin: 4px 0 0 0;
}

@media screen and (max-width: 768px) {
  .upload-row {
   justify-content: space-between;
  }
  .modern-card >>> .el-card__body{
    padding: 5px;
  }
  ::v-deep .el-table .el-table__cell{
    padding: 5px 0;
  }
  ::v-deep .el-table th.el-table__cell>.cell {
    padding-left: 5px!important;
    padding-right: 5px!important;
  }
}

</style>