/**
 * 
 * 
 */
export class DownloadUtil {
    /**
     * 
     * @param {string} content - 
     * @param {string} filename - 
     * @param {string} mimeType - MIME
     */
    static downloadText(content, filename, mimeType = 'text/plain') {
        const blob = new Blob([content], { type: mimeType })
        this.downloadBlob(blob, filename)
    }

    /**
     * JSON
     * @param {Object} data - JSON
     * @param {string} filename - 
     */
    static downloadJSON(data, filename = 'data.json') {
        const content = JSON.stringify(data, null, 2)
        this.downloadText(content, filename, 'application/json')
    }

    /**
     * CSV
     * @param {Array} data - CSV
     * @param {string} filename - 
     * @param {Array} headers - 
     */
    static downloadCSV(data, filename = 'data.csv', headers = null) {
        let csvContent = ''
        
        // 
        if (headers && headers.length > 0) {
            csvContent += headers.join(',') + '\n'
        }
        
        // 
        data.forEach(row => {
            if (Array.isArray(row)) {
                csvContent += row.map(field => `"${field}"`).join(',') + '\n'
            } else if (typeof row === 'object') {
                const values = headers ? headers.map(header => row[header] || '') : Object.values(row)
                csvContent += values.map(field => `"${field}"`).join(',') + '\n'
            }
        })
        
        // BOM
        const BOM = '\uFEFF'
        this.downloadText(BOM + csvContent, filename, 'text/csv;charset=utf-8')
    }

    /**
     * 
     * @param {string} dataURL - DataURL
     * @param {string} filename - 
     */
    static downloadImage(dataURL, filename = 'image.png') {
        const link = document.createElement('a')
        link.download = filename
        link.href = dataURL
        document.body.appendChild(link)
        link.click()
        document.body.removeChild(link)
    }

    /**
     * Canvas
     * @param {HTMLCanvasElement} canvas - Canvas
     * @param {string} filename - 
     * @param {string} format - 
     * @param {number} quality - (0-1)
     */
    static downloadCanvas(canvas, filename = 'canvas.png', format = 'image/png', quality = 1.0) {
        const dataURL = canvas.toDataURL(format, quality)
        this.downloadImage(dataURL, filename)
    }

    /**
     * Blob
     * @param {Blob} blob - Blob
     * @param {string} filename - 
     */
    static downloadBlob(blob, filename) {
        const url = URL.createObjectURL(blob)
        const link = document.createElement('a')
        link.href = url
        link.download = filename
        document.body.appendChild(link)
        link.click()
        document.body.removeChild(link)
        URL.revokeObjectURL(url)
    }

    /**
     * URL
     * @param {string} url - URL
     * @param {string} filename - 
     */
    static downloadURL(url, filename) {
        const link = document.createElement('a')
        link.href = url
        link.download = filename
        link.target = '_blank'
        document.body.appendChild(link)
        link.click()
        document.body.removeChild(link)
    }

    /**
     * ZIP
     * @param {Array} files -  [{name: string, content: string|Blob}]
     * @param {string} zipName - ZIP
     */
    static async downloadZip(files, zipName = 'files.zip') {
        // JSZip
        const JSZip = (await import('jszip')).default
        const zip = new JSZip()

        // ZIP
        files.forEach(file => {
            if (file.content instanceof Blob) {
                zip.file(file.name, file.content)
            } else {
                zip.file(file.name, file.content)
            }
        })

        // ZIP
        try {
            const content = await zip.generateAsync({ type: 'blob' })
            this.downloadBlob(content, zipName)
            return true
        } catch (error) {
            console.error('ZIP:', error)
            return false
        }
    }

    /**
     * URLBlob
     * @param {string} url - URL
     * @returns {Promise<Blob>} Blob
     */
    static async fetchBlob(url) {
        try {
            const response = await fetch(url)
            if (!response.ok) {
                throw new Error(`HTTP error! status: ${response.status}`)
            }
            return await response.blob()
        } catch (error) {
            console.error(':', error)
            throw error
        }
    }

    /**
     * 
     * @param {string} baseName - 
     * @param {string} extension - 
     * @returns {string} 
     */
    static generateTimestampFilename(baseName, extension) {
        const timestamp = new Date().toISOString().replace(/[:.]/g, '-').slice(0, -5)
        return `${baseName}_${timestamp}.${extension}`
    }

    /**
     * 
     * @param {number} bytes - 
     * @returns {string} 
     */
    static formatFileSize(bytes) {
        if (bytes === 0) return '0 Bytes'
        
        const k = 1024
        const sizes = ['Bytes', 'KB', 'MB', 'GB', 'TB']
        const i = Math.floor(Math.log(bytes) / Math.log(k))
        
        return parseFloat((bytes / Math.pow(k, i)).toFixed(2)) + ' ' + sizes[i]
    }
}

export default DownloadUtil
