<script>
import exifr from 'exifr';
import * as XLSX from 'xlsx';

export default {
  name: 'ExifView',
  data() {
    return {
      metaInfo: {
        title: '(EXIF) - GPS//',
        meta: [
          { name: 'description', content: ' EXIF GPSJPG/HEIC' },
          { name: 'keywords', content: 'EXIF,,GPS,EXIF,EXIF,' }
        ]
      },
      imgs: [],
      multipleSelection: [],
      loading: false,
      exifKeys: [
        { key: 'GPSLatitude', label: '' },
        { key: 'GPSLongitude', label: '' },
        { key: 'GPSAltitude', label: '' },
        { key: 'GPSLatitudeRef', label: '' },
        { key: 'GPSLongitudeRef', label: '' },
        { key: 'GPSAltitudeRef', label: '' },
        { key: 'GPSDateStamp', label: 'GPS' },
        { key: 'GPSTimeStamp', label: 'GPS' },
        { key: 'Make', label: '' },
        { key: 'Model', label: '' },
        { key: 'LensModel', label: '' },
        { key: 'Software', label: '' },
        { key: 'DateTimeOriginal', label: '' },
        { key: 'ModifyDate', label: '' },
        { key: 'CreateDate', label: '' },
        { key: 'FNumber', label: '' },
        { key: 'ExposureTime', label: '' },
        { key: 'ISOSpeedRatings', label: '' },
        { key: 'ISO', label: '' },
        { key: 'FocalLength', label: '' },
        { key: 'FocalLengthIn35mmFormat', label: '35mm' },
        { key: 'ExposureCompensation', label: '' },
        { key: 'ExposureProgram', label: '' },
        { key: 'MeteringMode', label: '' },
        { key: 'Flash', label: '' },
        { key: 'WhiteBalance', label: '' },
        { key: 'Orientation', label: '' },
        { key: 'ImageWidth', label: '' },
        { key: 'ImageHeight', label: '' },
        { key: 'PixelXDimension', label: '' },
        { key: 'PixelYDimension', label: '' }
      ],
    }
  },
  methods: {
    toDecimal(coord) {
      if (Array.isArray(coord)) {
        return (coord[0] || 0) + (coord[1] || 0) / 60 + (coord[2] || 0) / 3600;
      }
      return Number(coord);
    },
    async handleUploadChange(file, fileList) {
      // 
      this.imgs.forEach(img => img.previewUrl && URL.revokeObjectURL(img.previewUrl));
      this.imgs = [];
      this.loading = false;
      for (const f of fileList) {
        const rawFile = f.raw || f;
        this.imgs.push({
          file: rawFile,
          name: rawFile.name,
          exif: {},
          previewUrl: URL.createObjectURL(rawFile),
          status: '',
        });
      }
    },
    async handleParseAllExif() {
      this.loading = true;
      const mapKey = 'W5YBZ-JQ3EQ-VMQ5U-GQQ54-PFVDQ-LUBGC';
      for (let i = 0; i < this.imgs.length; i++) {
        const img = this.imgs[i];
        img.status = '...';
        try {
          img.exif = await exifr.parse(img.file);
          img.status = '';
          // locationMarker
          const lat = this.toDecimal(img.exif.GPSLatitude);
          const lng = this.toDecimal(img.exif.GPSLongitude);
          if (
            typeof lat === 'number' && !isNaN(lat) &&
            typeof lng === 'number' && !isNaN(lng)
          ) {
            img.mapUrl = `https://apis.map.qq.com/tools/poimarker?type=0&marker=coord:${lat},${lng};title:${img.name};addr:&key=${mapKey}&referer=web-tool`;
          } else {
            img.mapUrl = '';
          }
          // exifKeyskey
          const knownKeys = this.exifKeys.map(item => item.key);
          img.otherExif = Object.entries(img.exif)
            .filter(([k]) => !knownKeys.includes(k))
            .map(([k, v]) => `${k}: ${v}`)
            .join('; ');
        } catch (e) {
          img.exif = { error: 'EXIF' };
          img.status = '';
          img.mapUrl = '';
          img.otherExif = '';
        }
      }
      this.loading = false;
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleDeleteRow(index) {
      const img = this.imgs[index];
      if (img.previewUrl) URL.revokeObjectURL(img.previewUrl);
      this.imgs.splice(index, 1);
    },
    handleBatchDelete() {
      this.multipleSelection.forEach(img => img.previewUrl && URL.revokeObjectURL(img.previewUrl));
      this.imgs = this.imgs.filter(img => !this.multipleSelection.includes(img));
      this.multipleSelection = [];
    },
    handleExportExcel() {
      if (this.imgs.length === 0) {
        this.$message.warning('');
        return;
      }
      // 
      const header = ['', ...this.exifKeys.map(item => item.label)];
      const data = this.imgs.map(img => {
        const exif = img.exif || {};
        return [
          `'${img.name}`,
          ...this.exifKeys.map(item => exif[item.key] !== undefined && exif[item.key] !== null ? `'${exif[item.key]}` : '')
        ];
      });
      const ws = XLSX.utils.aoa_to_sheet([header, ...data]);
      const wb = XLSX.utils.book_new();
      XLSX.utils.book_append_sheet(wb, ws, 'EXIF');
      const now = new Date();
      const pad = n => n.toString().padStart(2, '0');
      const timeStr = `${now.getFullYear()}${pad(now.getMonth()+1)}${pad(now.getDate())}_${pad(now.getHours())}${pad(now.getMinutes())}${pad(now.getSeconds())}`;
      XLSX.writeFile(wb, `EXIF_${timeStr}.xlsx`);
    },
    beforeDestroy() {
      this.imgs.forEach(img => img.previewUrl && URL.revokeObjectURL(img.previewUrl));
    }
  }
}
</script>

<template>
  <div class="modern-bg">
    <el-row :gutter="0" justify="center">
      <el-col :xs="{span: 24, offset: 0}" :sm="{span: 22, offset: 1}" :md="{span: 20, offset: 2}" :lg="{span: 18, offset: 3}" :xl="{span: 16, offset: 4}">
        <el-card shadow="hover" class="modern-card">
          <div class="upload-row">
            <el-upload
              action="#"
              :auto-upload="false"
              :multiple="true"
              :show-file-list="false"
              accept="image/*"
              :disabled="loading"
              :on-change="handleUploadChange"
            >
              <el-button type="primary" round :disabled="loading"></el-button>
            </el-upload>
            <el-button type="success" size="mini" :disabled="imgs.length === 0 || loading" @click="handleParseAllExif"></el-button>
            <el-button size="mini" @click="$refs.imgTable.clearSelection()"></el-button>
            <el-button type="danger" size="mini" :disabled="multipleSelection.length === 0" @click="handleBatchDelete"></el-button>
            <el-button type="success" size="mini" :disabled="imgs.length === 0" @click="handleExportExcel">Excel</el-button>
          </div>
          <div style="margin-top: 24px;">
            <div class="table-scroll-x">
              <el-table
                ref="imgTable"
                :data="imgs"
                border
                style="width: auto; min-width: 900px; margin: 0 auto;"
               show-summary
                :summary-method="() => []"
                :scrollbar-always-on="true"
                @selection-change="handleSelectionChange"
              >
                <el-table-column type="selection" width="50"></el-table-column>
                <el-table-column label="" align="center" width="80">
                  <template slot-scope="scope">
                    <span>{{ scope.row.status }}</span>
                  </template>
                </el-table-column>
                <el-table-column label="" align="center" width="90">
                  <template slot-scope="scope">
                    <el-button type="danger" size="mini" @click="handleDeleteRow(scope.$index)"></el-button>
                  </template>
                </el-table-column>
                <el-table-column label="" align="center" width="100" fixed="left">
                  <template slot-scope="scope">
                    <el-image
                      style="width: 60px; height: 60px; border-radius: 8px; box-shadow: 0 2px 8px rgba(0,0,0,0.08);"
                      :src="scope.row.previewUrl"
                      :preview-src-list="imgs.map(i => i.previewUrl)"
                      :alt="scope.row.name"
                    />
                  </template>
                </el-table-column>
                <el-table-column label="" prop="name" align="center" width="180" />
                <el-table-column label="" align="center" width="80">
                  <template slot-scope="scope">
                    <a v-if="scope.row.mapUrl" :href="scope.row.mapUrl" target="_blank"></a>
                    <span v-else style="color:#ccc;">-</span>
                  </template>
                </el-table-column>
                <el-table-column v-for="item in exifKeys" :key="item.key" :label="item.label" align="center">
                  <template slot-scope="scope">
                    <span v-if="item.key === 'GPSLatitude' || item.key === 'GPSLongitude'">
                      {{ toDecimal(scope.row.exif[item.key]) || '' }}
                    </span>
                    <span v-else>
                      {{ scope.row.exif[item.key] || '' }}
                    </span>
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<style scoped>
.modern-bg {
  //min-height: 100vh;
  background: #f6f8fa;
  //padding: 32px 0;
}
.modern-card {
  border-radius: 16px;
  box-shadow: 0 4px 24px rgba(0,0,0,0.08);
  margin-bottom: 32px;
}
.upload-row {
  display: flex;
  align-items: center;
  gap: 16px;
  flex-wrap: wrap;
  justify-content: center;
}
.table-scroll-x {
  width: 100%;
  overflow-x: auto;
}

@media screen and (max-width: 768px) {
  .modern-card > .el-card__body {
    padding: 5px !important;
  }
  .el-table th.el-table__cell > .cell {
    padding-left: 5px !important;
    padding-right: 5px !important;
  }
}
</style> 