// 
const generalRoutes = [
    {
        path: '/',
        name: ' ',
        component: Home,
        meta: { group: 'general' }
    },
    {
        path: '/about',
        name: ' ',
        component: About,
        meta: { group: 'general' }
    }
];

const router = new Router({
    mode: 'history',  //  history SEO
    routes: [
        // 
        ...generalRoutes,
        // 
        ...imageRoutes,
        // 
        ...documentRoutes,
        // 
        ...textRoutes,
        // 
        ...devRoutes,
        // 
        ...mediaRoutes,
        // 
        ...generatorRoutes,
        // 
        ...timeRoutes,
        // 
        ...utilityRoutes,
        // 
        ...entertainmentRoutes,
        // 
        ...calculatorRoutes
    ],
    //  - 
    scrollBehavior(to, from, savedPosition) {
        // 
        if (savedPosition) {
            return savedPosition;
        }
        // 
        return { x: 0, y: 0 };
    }
});

//  - SEO
router.beforeEach((to, from, next) => {
    // SEO
    SeoUtil.clearDynamicSeoTags();

    // SEO
    const seoConfig = SeoUtil.getRouteSeoConfig(to.name);

    // SEO
    SeoUtil.setPageSeo(seoConfig);

    // 
    SeoUtil.setLanguage('zh-CN');

    // historySEO
    SeoUtil.optimizeHashRoute(to.fullPath);

    next();
});

// 
export const routeGroups = {
    general: {
        name: '',
        routes: generalRoutes
    },
    image: {
        name: '',
        routes: imageRoutes
    },
    document: {
        name: '',
        routes: documentRoutes
    },
    text: {
        name: '',
        routes: textRoutes
    },
    dev: {
        name: '',
        routes: devRoutes
    },
    media: {
        name: '',
        routes: mediaRoutes
    },
    generator: {
        name: '',
        routes: generatorRoutes
    },
    time: {
        name: '',
        routes: timeRoutes
    },
    utility: {
        name: '',
        routes: utilityRoutes
    },
    entertainment: {
        name: '',
        routes: entertainmentRoutes
    },
    calculator: {
        name: '',
        routes: calculatorRoutes
    }
};

router.afterEach((to) => {
    if (typeof window.gtag === 'function') {
        setTimeout(() => {
            window.gtag('event', 'page_view', {
                page_path: to.fullPath,
                page_title: to.meta.title || document.title,
                send_to: 'G-MTDVMYZDBN' //  ID
            });
            // console.log(`[GA] : ${to.fullPath}`); // 
        }, 0); //  0ms
    }
});

// Electron
export const routes = router.options.routes;

export default router;
