          description: ''
        }
      ],
      // 
      technologies: [
        { name: 'Vue.js', description: '', icon: '' },
        { name: 'Element UI', description: 'UI', icon: '' },
        { name: 'JavaScript', description: '', icon: '' },
        { name: 'HTML5 Canvas', description: '', icon: '' },
        { name: 'WebAssembly', description: '', icon: '' },
        { name: 'PWA', description: '', icon: '' }
      ],
      // 
      timeline: [
        {
          date: '20241',
