        }
      ],
      // 
      qrCodeDataUrl: null,
      qrCodeContent: null
    }
  },
  methods: {
    // 
    async parseExistingQRCode() {
      // 
      //console.log('');

      try {
        console.log('...');

        //  assets 
        const qrContent = await this.readExistingQRCode('/alipay-qr.jpg');

        if (this.isAlipayQRCode(qrContent)) {
          console.log(':', qrContent);

          // 
          const qrDataUrl = await this.generateQRCode(qrContent, 200);

          // 
          this.qrCodeContent = qrContent;
          this.qrCodeDataUrl = qrDataUrl;

          console.log('');
        } else {
          console.log('');
        }
      } catch (error) {
        console.log(':', error.message);
        // 
      }

    },

    // 
    triggerFileInput() {
      this.$refs.fileInput.click();
    },

    // 
    async handleQRUpload(/* event */) {
      // 
      console.log('');
      /*
      const file = event.target.files[0];
      if (!file) return;

      try {
        // 
        this.showLoading('...');

        // 
        const content = await readQRCode(file);

        // 
        if (!isAlipayQRCode(content)) {
          alert('');
          return;
        }

        // 
        const qrDataUrl = await generateQRCode(content, 200);

        // 
        this.qrCodeContent = content;
        this.qrCodeDataUrl = qrDataUrl;

        // 
        localStorage.setItem('alipay_qr_content', content);
        localStorage.setItem('alipay_qr_image', qrDataUrl);

        alert('');

      } catch (error) {
        console.error(':', error);
        alert('' + error.message);
      } finally {
        this.hideLoading();
      }
      */
    },

    // 
    showLoading(message) {
      console.log(message);
    },

    // 
    hideLoading() {
      console.log('');
    },

    async readExistingQRCode() {
      // 
      return 'https://qr.alipay.com/fkx123456789'
    },

    isAlipayQRCode(content) {
      // 
      return content && (content.includes('alipay.com') || content.includes('qr.alipay.com'))
    },

    async generateQRCode(content, size = 200) {
      // QRCode
      try {
        console.log(':', content, size)
        const qrDataUrl = await QRCode.toDataURL(content, {
          width: size,
          margin: 2,
          color: {
            dark: '#000000',
            light: '#FFFFFF'
          }
        })
        return qrDataUrl
      } catch (error) {
        console.error(':', error)
        // 
        return `data:image/svg+xml;base64,${btoa(`
          <svg width="${size}" height="${size}" xmlns="http://www.w3.org/2000/svg">
            <rect width="${size}" height="${size}" fill="#f0f0f0"/>
            <text x="50%" y="45%" text-anchor="middle" font-family="Arial" font-size="14" fill="#666">
              
            </text>
            <text x="50%" y="60%" text-anchor="middle" font-family="Arial" font-size="12" fill="#666">
              
            </text>
          </svg>
        `)}`
      }
    }
  },

  // 
  async mounted() {
    await this.parseExistingQRCode();

  }
}
