import Compressor from 'compressorjs';
import FileUtil from "@/util/FileUtil";
import imageCompression from 'browser-image-compression';
const { Image } = require('image-js');

/**
 * 
 */
export default class ImgUtil extends FileUtil {

    /**
     * 
     * @param {File} file 
     * @param {number} quality  (0-1)
     * @param {string} mimeType 
     * @returns {Promise<File>} 
     */
    static compressImage(file, quality = 0.6, mimeType = 'image/png') {
        return new Promise((resolve, reject) => {
            new Compressor(file, {
                quality: quality,
                mimeType: mimeType,
                success: resolve,
                error: reject,
            });
        });
    }

    /**
     * 
     * @param {string} file_url URL
     * @param {string} format 
     * @param {number} quality 
     */
    static async changeExt(file_url, format, quality = 100) {
        //  PNG 
        //const image = await Image.load(file_url);

        Image.load(file_url).then(image => {
            //  PNG  JPEG 
            image.save('output.jpg', {quality: quality, format: format});

            console.log(' JPEG ');
        }).catch(err => {
            console.error('', err);
        });
    }

    /**
     * 
     * @param {File} file 
     * @param {string} fileType 
     * @returns {Promise<File>} 
     */
    static async changeExtension(file, fileType = 'image/jpeg') {
        const options = {
            // maxSizeMB: 1, //  1MB
            // maxWidthOrHeight: 1920, //  1920px
            useWebWorker: true, //  Web Worker 
            fileType: fileType, //  JPEG 
        };

        // 
        const compressedFile = await imageCompression(file, options);
        return compressedFile;
        //  URL
        //this.compressedUrl = URL.createObjectURL(compressedFile);
    }

    /**
     * Canvas
     * @param {File} file 
     * @param {number} quality  (0-1)
     * @param {number} maxWidth 
     * @param {number} maxHeight 
     * @returns {Promise<File>} 
     */
    static advancedCompressImage(file, quality = 0.8, maxWidth = 1920, maxHeight = 1080) {
        return new Promise((resolve) => {
            const canvas = document.createElement('canvas');
            const ctx = canvas.getContext('2d');
            const img = new Image();
            
            img.onload = () => {
                // 
                let { width, height } = img;
                
                if (width > maxWidth) {
                    height = (height * maxWidth) / width;
                    width = maxWidth;
                }
                
                if (height > maxHeight) {
                    width = (width * maxHeight) / height;
                    height = maxHeight;
                }
                
                canvas.width = width;
                canvas.height = height;
                
                // 
                ctx.drawImage(img, 0, 0, width, height);
                
                // Blob
                canvas.toBlob((blob) => {
                    const compressedFile = new File([blob], file.name, {
                        type: file.type,
                        lastModified: Date.now()
                    });
                    resolve(compressedFile);
                }, file.type, quality);
            };
            
            img.src = URL.createObjectURL(file);
        });
    }

    /**
     * 
     * @param {File} file 
     * @returns {Promise<{width: number, height: number}>} 
     */
    static getImageDimensions(file) {
        return new Promise((resolve, reject) => {
            const img = new Image();
            img.onload = () => {
                resolve({
                    width: img.naturalWidth,
                    height: img.naturalHeight
                });
                URL.revokeObjectURL(img.src);
            };
            img.onerror = reject;
            img.src = URL.createObjectURL(file);
        });
    }

    /**
     * 
     * @param {File} file 
     * @param {number} size 
     * @returns {Promise<string>} DataURL
     */
    static createThumbnail(file, size = 150) {
        return new Promise((resolve) => {
            const canvas = document.createElement('canvas');
            const ctx = canvas.getContext('2d');
            const img = new Image();

            img.onload = () => {
                const { width, height } = img;
                const scale = Math.min(size / width, size / height);
                const newWidth = width * scale;
                const newHeight = height * scale;

                canvas.width = size;
                canvas.height = size;

                // 
                const x = (size - newWidth) / 2;
                const y = (size - newHeight) / 2;

                ctx.fillStyle = '#f0f0f0';
                ctx.fillRect(0, 0, size, size);
                ctx.drawImage(img, x, y, newWidth, newHeight);

                resolve(canvas.toDataURL());
                URL.revokeObjectURL(img.src);
            };

            img.src = URL.createObjectURL(file);
        });
    }


}
