/**
 * SEO - Vue2 Hash
 */
export class SeoUtil {
    /**
     * 
     * @param {string} title - 
     * @param {string} suffix - 
     */
    static setTitle(title, suffix = ' - ') {
        if (typeof document !== 'undefined') {
            document.title = title + suffix;
        }
    }

    /**
     * meta
     * @param {string} name - meta
     * @param {string} content - meta
     */
    static setMeta(name, content) {
        if (typeof document === 'undefined') return;
        
        let meta = document.querySelector(`meta[name="${name}"]`);
        if (!meta) {
            meta = document.createElement('meta');
            meta.setAttribute('name', name);
            meta.setAttribute('data-dynamic', 'true');
            document.head.appendChild(meta);
        }
        meta.setAttribute('content', content);
    }

    /**
     * 
     * @param {string} description - 
     */
    static setDescription(description) {
        this.setMeta('description', description);
    }

    /**
     * 
     * @param {string|Array} keywords - 
     */
    static setKeywords(keywords) {
        const keywordStr = Array.isArray(keywords) ? keywords.join(', ') : keywords;
        this.setMeta('keywords', keywordStr);
    }

    /**
     * Open Graph
     * @param {Object} ogData - OG
     */
    static setOpenGraph(ogData) {
        if (typeof document === 'undefined') return;
        
        const {
            title,
            description,
            image,
            url,
            type = 'website',
            siteName = ''
        } = ogData;

        if (title) this.setProperty('og:title', title);
        if (description) this.setProperty('og:description', description);
        if (type) this.setProperty('og:type', type);
        if (siteName) this.setProperty('og:site_name', siteName);
        if (image) this.setProperty('og:image', image);
        if (url) this.setProperty('og:url', url);
    }

    /**
     * propertymeta
     * @param {string} property - property
     * @param {string} content - 
     */
    static setProperty(property, content) {
        if (typeof document === 'undefined') return;
        
        let meta = document.querySelector(`meta[property="${property}"]`);
        if (!meta) {
            meta = document.createElement('meta');
            meta.setAttribute('property', property);
            meta.setAttribute('data-dynamic', 'true');
            document.head.appendChild(meta);
        }
        meta.setAttribute('content', content);
    }

    /**
     * SEO
     * @param {string} routeName - 
     * @returns {Object} SEO
     */
    static getRouteSeoConfig(routeName) {
        const baseUrl = 'https://tool.4yop.com'; // 
        
        const seoConfigs = {
            '': {
                title: ' - ',
                description: 'OCRPDF',
                keywords: ['', '', '', '', 'OCR', 'PDF'],
                url: baseUrl
            },
            '': {
                title: ' - ',
                description: '',
                keywords: ['', '', ''],
                url: `${baseUrl}/about`
            },
            '|': {
                title: ' - ',
                description: 'JPGPNGWebP',
                keywords: ['', '', 'JPG', 'PNG', 'WebP'],
                url: `${baseUrl}/img/compress`
            },
            '|': {
                title: 'AI - ',
                description: 'AI',
                keywords: ['', '', 'AI', '', ''],
                url: `${baseUrl}/img/matting`
            },
            '(EXIF)': {
                title: 'EXIF - ',
                description: 'EXIF',
                keywords: ['EXIF', '', '', '', ''],
                url: `${baseUrl}/img/exif`
            },
            '': {
                title: 'AI - ',
                description: 'AI',
                keywords: ['', 'AI', '', ''],
                url: `${baseUrl}/img/upscale`
            },
            '/': {
                title: ' - ',
                description: 'RGBHEX',
                keywords: ['', '', '', 'RGB', 'HEX'],
                url: `${baseUrl}/img/color-analyze`
            },
            '': {
                title: ' - ',
                description: '',
                keywords: ['', '', '', ''],
                url: `${baseUrl}/img/scramble`
            },
            '': {
                title: 'AI - ',
                description: 'AI',
                keywords: ['', 'AI', '', ''],
                url: `${baseUrl}/img/art-style`
            },
            '': {
                title: 'OCR - ',
                description: 'OCRExcel',
                keywords: ['OCR', '', '', ''],
                url: `${baseUrl}/img/ocr`
            },
            'pdf': {
                title: 'PDF - PDF',
                description: 'PDFJPGPNG',
                keywords: ['PDF', 'PDF', 'PDFJPG', 'PDFPNG'],
                url: `${baseUrl}/office/convert`
            }
        };

        return seoConfigs[routeName] || {
            title: routeName + ' - ',
            description: '',
            keywords: ['', routeName],
            url: baseUrl
        };
    }

    /**
     * SEO
     * @param {Object} seoData - SEO
     */
    static setPageSeo(seoData) {
        const {
            title,
            description,
            keywords,
            image,
            url
        } = seoData;

        // 
        if (title) this.setTitle(title);
        if (description) this.setDescription(description);
        if (keywords) this.setKeywords(keywords);

        // Open Graph
        if (title || description || image || url) {
            this.setOpenGraph({
                title,
                description,
                image,
                url
            });
        }

        // canonical
        if (url) {
            this.setCanonical(url);
        }
    }

    /**
     * canonical
     * @param {string} url - canonical URL
     */
    static setCanonical(url) {
        if (typeof document === 'undefined') return;
        
        let canonical = document.querySelector('link[rel="canonical"]');
        if (!canonical) {
            canonical = document.createElement('link');
            canonical.setAttribute('rel', 'canonical');
            canonical.setAttribute('data-dynamic', 'true');
            document.head.appendChild(canonical);
        }
        canonical.setAttribute('href', url);
    }

    /**
     * SEO
     */
    static clearDynamicSeoTags() {
        if (typeof document === 'undefined') return;
        
        // meta
        const dynamicMetas = document.querySelectorAll('meta[data-dynamic="true"]');
        dynamicMetas.forEach(meta => meta.remove());

        // link
        const dynamicLinks = document.querySelectorAll('link[data-dynamic="true"]');
        dynamicLinks.forEach(link => link.remove());
    }

    /**
     * 
     * @param {string} lang - 
     */
    static setLanguage(lang = 'zh-CN') {
        if (typeof document !== 'undefined') {
            document.documentElement.setAttribute('lang', lang);
        }
    }

    /**
     * hashSEO
     * @param {string} hash - hash
     */
    static optimizeHashRoute(hash) {
        // hashSEO
        if (typeof document === 'undefined') return;
        
        // canonical
        const baseUrl = 'https://tool.4yop.com';
        const fullUrl = baseUrl + hash;
        this.setCanonical(fullUrl);
    }

    /**
     * 
     * @param {Object} structuredData - 
     */
    static addStructuredData(structuredData) {
        if (typeof document === 'undefined') return;
        
        // 
        const existingScripts = document.querySelectorAll('script[type="application/ld+json"][data-dynamic="true"]');
        existingScripts.forEach(script => script.remove());

        const script = document.createElement('script');
        script.type = 'application/ld+json';
        script.setAttribute('data-dynamic', 'true');
        script.textContent = JSON.stringify(structuredData);
        document.head.appendChild(script);
    }
}
